:root[data-theme="theme-light"] {
  --primary-bg-color: rgba(255, 255, 255, 1);
  --secondary-bg-color: #f3f4f6;
  --primary-text-color: #000;
  --secondary-text-color: #4c4b4b;
}
:root[data-theme="theme-dark"] {
  --primary-bg-color: rgba(21, 21, 21, 1);
  --secondary-bg-color: rgba(32, 32, 34, 1);
  --primary-text-color: #fff;
  --secondary-text-color: #d1d5db;
}
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  word-wrap: break-word;
  line-height: 140%;
}
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  outline: none;
  color: inherit;
}
a:hover {
  cursor: pointer;
}
a:focus {
  outline: none;
  box-shadow: none;
}
img {
  outline: none;
  border: 0;
  vertical-align: middle;
}
ul {
  list-style: none;
}
b,
strong {
  font-weight: 700;
}
input:focus,
textarea:focus,
input:required,
textarea:required {
  outline: none;
  box-shadow: none;
}
input,
textarea,
select {
  border: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
input[type="submit"],
input[type="button"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
select::-ms-expand {
  display: none;
}
textarea {
  resize: vertical;
}
input[placeholder] {
  text-overflow: ellipsis;
}
::-moz-placeholder {
  text-overflow: ellipsis;
}
input:-moz-placeholder {
  text-overflow: ellipsis;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}
footer,
header,
nav {
  display: block;
}
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  scroll-behavior: smooth;
}
.clearfix:after,
.clearfix:before,
.container-fluid:after,
.container-fluid:before,
.container:after,
.container:before,
.row:after,
.row:before {
  content: " ";
  display: table;
}
.clearfix:after,
.container-fluid:after,
.container:after,
.row:after {
  clear: both;
}
/* scrollbar */
::-webkit-scrollbar {
  background: var(--primary-bg-color);
  width: 10px;
}
::-webkit-scrollbar-thumb {
  background: var(--secondary-bg-color);
  border-radius: 10px;
}
html {
  scrollbar-color: var(--secondary-bg-color);
  scrollbar-width: thin;
}

body {
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
}
.section-heading {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3.5px;
  padding: 50px 0;
  position: relative;
  span {
    background: var(--primary-bg-color);
    padding: 0 20px;
  }
  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;
    background: var(--secondary-bg-color);
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    z-index: -1;
  }
}
